<template>
  <div id="allmap" :class="{'allmap':'true','allmap_phone':$store.state.w_win<=850}"></div>
</template>
<script>
import BMap from 'BMap'

export default {
  name: 'adtress',
  props: {
    
  },
  components:{
  },
methods: {
    createMap () {
      /* eslint-disable */
      // Map
      var map = new BMap.Map("allmap")
      // ,
      map.centerAndZoom(new BMap.Point(124.325924,40.075798), 18)
      //
      map.addControl(new BMap.MapTypeControl({
        mapTypes:[BMAP_NORMAL_MAP, BMAP_HYBRID_MAP]
      }))
      //  
      map.setCurrentCity("")
      //
      map.enableScrollWheelZoom(true)
      /* eslint-enable */
      var point = new BMap.Point(124.325924,40.075798);   
      var marker = new BMap.Marker(point);        //    
      map.addOverlay(marker);                     // 
      var label = new BMap.Label('', {       // 
      position: point,                          // 
      // offset: new BMap.Size(10, 20)           // 
        })
      label.setStyle({                              // label
      color: '#00f',
      fontSize: '1rem',
      border: '0',
      backgroundColor: '#ff0',
      padding:'2px'
        })  
      map.addOverlay(label);                        // 
    }
  },
  mounted () {
    this.createMap()
  }


}
</script>
<style>
.allmap{
  height:20rem;
  width: 40rem;
  border:1px solid #ccc;
}
.allmap_phone{
  width: 90vw;
  height:45vw;
}
</style>
